.ztree * {
  padding: 0;
  margin: 0;
  font-size: 12px;
}

.ztree {
  margin: 0;
  padding: 5px;
  color: #333;
}

.ztree li {
  padding: 0;
  margin: 0;
  list-style: none;
  line-height: 25px;
  text-align: left;
  white-space: nowrap;
  outline: 0;
}

.ztree li ul {
  margin: 0;
  padding: 0 0 0 18px;
}

.ztree li ul.line {
  //background: url(./img/line_conn.gif) 0 0 repeat-y;
}

.ztree li a {
  padding: 1px 3px 0 0;
  margin: 0;
  cursor: pointer;
  //height: 17px;
  color: #333;
  background-color: transparent;
  text-decoration: none;
  vertical-align: top;
  display: inline-block;
}

//.ztree li a:hover {
//    text-decoration: underline
//}

.ztree li a.curSelectedNode {
  //padding-top: 0px;
  //height: 16px;
  .node_name {
    color: #e25956;
  }
}

.ztree li a.curSelectedNode_Edit {
  //padding-top: 0px;
  background-color: #ffe6b0;
  color: black;
  //height: 16px;
  //border: 1px #FFB951 solid;
  //opacity: 0.8;
}

.ztree li a.tmpTargetNode_inner {
  padding-top: 0px;
  background-color: #316ac5;
  color: white;
  //height: 16px;
  border: 1px #316ac5 solid;
  opacity: 0.8;
  filter: alpha(opacity=80);
}

.ztree li a.tmpTargetNode_prev {
}

.ztree li a.tmpTargetNode_next {
}

.ztree li a input.rename {
  //height: 14px;
  width: 80px;
  padding: 0;
  margin: 0;
  font-size: 12px;
  border: 1px #7ec4cc solid;
  *border: 0px;
}

.ztree li span {
  //line-height: 16px;
  margin-right: 2px;
}

.ztree li span.button {
  line-height: 0;
  margin: 0 2px;
  width: 19px;
  height: 22px;
  display: inline-block;
  //vertical-align: middle;
  vertical-align: top;
  border: 0 none;
  cursor: pointer;
  outline: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center;
  /*background-image: url("./img/zTreeStandard.png");*/
  /**background-image: url("./img/zTreeStandard.gif")*/
  // position: relative;
  &:after {
    font-family: 'iconfont';
    font-size: 14px;
    // position: absolute;
    // top: 4px;
    // left: 0;
    line-height: 1;
  }
}

