.ztree li span.button.root_open {
  //background-position: -92px -54px
  // background-image: url('../img/ztree-up.png');
  &:after {
    @extend %button;
    content: $ztreeOpen;
  }
}

.ztree li span.button.root_close {
  //background-position: -74px -54px
  // background-image: url('../img/ztree-down.png');
  &:after {
    content: $ztreeClose;
