.ztree li span.button.switch {
  width: 18px;
  height: 24px; }

.ztree li span.button.root_open:after {
  content: ""; }

.ztree li span.button.root_close:after {
  content: ""; }

.ztree li span.button.roots_open {
  /*background-position: -92px 0*/ }
  .ztree li span.button.roots_open:after {
    content: ""; }

.ztree li span.button.roots_close {
  /*background-position: -74px 0*/ }
  .ztree li span.button.roots_close:after {
    content: ""; }

.ztree li span.button.center_open:after {
  content: ""; }

.ztree li span.button.center_close:after {
  content: ""; }

.ztree li span.button.bottom_open {
  /*background-position: -92px -36px*/ }
  .ztree li span.button.bottom_open:after {
    content: ""; }

.ztree li span.button.bottom_close {
  /*background-position: -74px -36px*/ }
  .ztree li span.button.bottom_close:after {
    content: ""; }

.ztree li span.button.noline_open {
  background-position: -92px -72px; }

.ztree li span.button.noline_close {
  background-position: -74px -72px; }

.ztree li span.button.root_docu {
  background: none; }

.ztree li span.button.roots_docu {
  background-position: -56px 0; }
  .ztree li span.button.roots_docu:after {
    content: ''; }

.ztree li span.button.center_docu:after {
  content: ''; }

.ztree li span.button.bottom_docu:after {
  content: ''; }

.ztree li span.button.noline_docu {
  background: none; }

.ztree li span.button.ico_open {
  background-image: url("../img/tree-icon.png"); }

.ztree li span.button.ico_close {
  background-image: url("../img/tree-icon.png"); }

.ztree li span.button.ico_docu {
  background-image: url("../img/tree-icon.png"); }

.ztree li span.button.edit:after {
  content: '\e8cf';
  font-size: 15px;
  line-height: 1.5; }

.ztree li span.button.remove:after {
  content: '\e61c';
  font-size: 15px;
  line-height: 1.5; }

.ztree li span.button.add:after {
  content: '\e625';
  font-size: 15px;
  line-height: 1.5; }

ul.tmpTargetzTree {
  background-color: #ffe6b0;
  opacity: 0.8;
  filter: alpha(opacity=80); }

span.tmpzTreeMove_arrow {
  width: 16px;
  height: 16px;
  display: inline-block;
  padding: 0;
  margin: 2px 0 0 1px;
  border: 0 none;
  position: absolute;
  background-color: transparent;
  background-repeat: no-repeat;
  background-attachment: scroll; }

ul.ztree.zTreeDragUL {
  margin: 0;
  padding: 0;
  position: absolute;
  width: auto;
  height: auto;
  overflow: hidden;
  background-color: #cfcfcf;
  border: 1px #00b83f dotted;
  opacity: 0.8;
  filter: alpha(opacity=80); }

.zTreeMask {
  z-index: 10000;
  background-color: #cfcfcf;
  opacity: 0;
  filter: alpha(opacity=0);
  position: absolute; }

